.art-video-player {
  display: flex;
  position: relative;
  margin: 0 auto;
  z-index: 10;
  width: 100%;
  height: 100%;
  outline: 0;
  zoom: 1;
  font-family: Roboto, Arial, Helvetica, sans-serif;
  color: #eee;
  text-align: left;
  direction: ltr;
  font-size: 14px;
  line-height: 1.3;
  user-select: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  touch-action: manipulation;
  -ms-high-contrast-adjust: none; }
  .art-video-player * {
    margin: 0;
    padding: 0;
    box-sizing: border-box; }
  .art-video-player ::-webkit-scrollbar {
    width: 5px; }
  .art-video-player ::-webkit-scrollbar-thumb {
    background-color: #666; }
  .art-video-player ::-webkit-scrollbar-thumb:hover {
    background-color: #ccc; }
  .art-video-player .art-icon {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    line-height: 1.5; }
    .art-video-player .art-icon svg {
      fill: #fff; }
  .art-video-player > > > object {
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    overflow: hidden;
    pointer-events: none;
    z-index: -1; }
  @supports (-webkit-backdrop-filter: initial) or (backdrop-filter: initial) {
    .art-video-player .art-backdrop-filter {
      -webkit-backdrop-filter: saturate(180%) blur(20px);
      backdrop-filter: saturate(180%) blur(20px);
      background-color: rgba(0, 0, 0, 0.7) !important; } }
