.art-video-player .art-pip-header {
  display: none;
  position: absolute;
  z-index: 110;
  left: 0;
  top: 0;
  right: 0;
  height: 35px;
  line-height: 35px;
  color: #fff;
  background: rgba(0, 0, 0, 0.5);
  align-items: center;
  justify-content: space-between;
  opacity: 0;
  visibility: hidden;
  transition: all 0.2s ease-in-out; }
  .art-video-player .art-pip-header .art-pip-title {
    flex: 1;
    padding: 0 10px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    cursor: move; }
  .art-video-player .art-pip-header .art-pip-close {
    width: 35px;
    text-align: center;
    font-size: 22px;
    cursor: pointer; }

.art-video-player.art-is-dragging {
  opacity: 0.7; }

.art-video-player.art-pip {
  position: fixed;
  z-index: 9999;
  width: 400px;
  height: 225px;
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 3px 6px 0 rgba(0, 0, 0, 0.2); }
  .art-video-player.art-pip .art-pip-header {
    display: flex;
    user-select: none; }
  .art-video-player.art-pip.art-hover .art-pip-header {
    opacity: 1;
    visibility: visible; }
  .art-video-player.art-pip .art-mask .art-state {
    position: static; }
  .art-video-player.art-pip .art-contextmenu,
  .art-video-player.art-pip .art-bottom,
  .art-video-player.art-pip .art-danmu,
  .art-video-player.art-pip .art-info,
  .art-video-player.art-pip .art-layers,
  .art-video-player.art-pip .art-notice,
  .art-video-player.art-pip .art-setting,
  .art-video-player.art-pip .art-subtitle {
    display: none !important; }
